import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  DatePicker,
  Empty,
  Input,
  Tooltip,
  Select,
  Row,
  Divider,
  Table
  // message,
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/deliveryOrderDetailReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectDO = "/dashboard/sales/delivery-orders";
const redirectSO = "/dashboard/sales/sales-orders";

const expandedRowRender = data => {
  const columns = [
    {
      title: "No",
      dataIndex: "index_no",
      width: "2%"
    },
    {
      title: getLanguage({ id: "items.code" }),
      dataIndex: "item_code",
      width: "15%"
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "totalQtyDeliveredl" }),
      dataIndex: "delivery_qty",
      width: "10%",
      align: "right",
      render: (text, row) => (
        <NumberFormat
          value={parseFloat(text)}
          suffix={" " + row.unit.unit_name}
        />
      )
    },
    // {
    //   title: "Billed Quantity",
    //   dataIndex: "billed_qty", // dari PR
    //   width: "10%",
    //   align: "right",
    //   render: text => <NumberFormat value={parseFloat(text)} />
    // },
    {
      title: getLanguage({ id: "returnedQty" }),
      dataIndex: "returned_qty", // dari PR
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={parseFloat(text)} />
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        style={{ marginBottom: "5px", marginTop: "5px" }}
      />
    </div>
  );
};

class DeliveryOrderDetailReport extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "delivery_order_date",
      sort_type: "asc"
      // pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getDeliveryOrderDetailReports } = this.props;
    getDeliveryOrderDetailReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.deliveryOrderDetailReport.error) {
      notification("error", this.props.deliveryOrderDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.deliveryOrderDetailReport.success) {
      notification("success", this.props.deliveryOrderDetailReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getDeliveryOrderDetailReports } = this.props;
    let filter = this.filterValue();

    getDeliveryOrderDetailReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getDeliveryOrderDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "delivery_order_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null });
    getDeliveryOrderDetailReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getDeliveryOrderDetailReports } = this.props;
    //const { current_page } = this.props.deliveryOrderDetailReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getDeliveryOrderDetailReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getDeliveryOrderDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "delivery_order_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null });
    getDeliveryOrderDetailReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateDeliveryOrderReport(id);
    } else {
      this.props.deactivateDeliveryOrderReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getDeliveryOrderDetailReports } = this.props;
  //   const { search, filter } = this.props.deliveryOrderDetailReport;
  //   getDeliveryOrderDetailReports(page, search, filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "deliveryOrders.no" }),
        dataIndex: "delivery_order_no",
        rowKey: "delivery_order_no",
        width: "16%",
        // fixed: "left",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "approved";
              break;
            case 3:
              className = "rejected";
              break;
            case 4:
              className = "invoiced";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link to={`${redirectDO}/update/${row.id_delivery_order}`}>
                  {row.status === 3 ? getLanguage({ id: "rejected" }) : text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "deliveryOrders.date" }),
        date: true,
        dataIndex: "delivery_order_date",
        rowKey: "delivery_order_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "salesOrders.salesOrderNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "16%",
        render: (text, row) => {
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <TransNo>
                <Link to={`${redirectSO}/update/${row.id_sales_order}`}>
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "13%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "12%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "totalQtyOrder" }),
        dataIndex: "total_qty_order",
        rowKey: "total_qty_order",
        width: "11%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "totalQtyDelivered" }),
        dataIndex: "total_qty_delivered",
        rowKey: "total_qty_delivered",
        width: "11%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },

      {
        title: getLanguage({ id: "salesOrders.createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "7%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "confirmedBy" }),
        dataIndex: "confirmed_by",
        rowKey: "confirmed_by",
        width: "7%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "7%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.status) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "approved";
              display = getLanguage({ id: "approved" });
              break;
            case 3:
              className = "rejected";
              display = getLanguage({ id: "rejected" });
              break;
            case 4:
              className = "invoiced";
              display = getLanguage({ id: "invoiced" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const excelColumns = [
      {
        title: getLanguage({ id: "deliveryOrders.no" }),
        dataIndex: "delivery_order_no",
        rowKey: "delivery_order_no",
        width: "16%"
      },
      {
        title: getLanguage({ id: "deliveryOrders.date" }),
        date: true,
        dataIndex: "delivery_order_date",
        rowKey: "delivery_order_date",
        width: "13%"
      },
      {
        title: getLanguage({ id: "salesOrders.salesOrderNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "16%"
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "13%"
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "12%"
      },
      {
        title: getLanguage({ id: "TotalQtyOrder" }),
        dataIndex: "total_qty_order",
        rowKey: "total_qty_order",
        width: "11%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "totalDeliveredQty" }),
        dataIndex: "total_qty_delivered",
        rowKey: "total_qty_delivered",
        width: "11%",
        align: "right",
        parse: true
      },

      {
        title: getLanguage({ id: "salesOrders.createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "7%"
      },
      {
        title: getLanguage({ id: "confirmedBy" }),
        dataIndex: "confirmed_by",
        rowKey: "confirmed_by",
        width: "7%"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "7%",
        align: "center"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%"
      },
      {
        title: getLanguage({ id: "totalQtyDeliveredl" }),
        dataIndex: "delivery_qty",
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "deliveredUnit" }),
        dataIndex: "unit.unit_name",
        width: "10%"
        // align: "right",
        // parse: true
      },
      // {
      //   title: "Billed Quantity",
      //   dataIndex: "billed_qty", // dari PR
      //   width: "10%",
      //   align: "right",
      //   parse: true
      // },
      {
        title: getLanguage({ id: "returnedQty" }),
        dataIndex: "returned_qty", // dari PR
        width: "10%",
        align: "right",
        parse: true
      }
    ];
    // console.log(this.props);

    // Data setup
    const { deliveryOrderDetailReport } = this.props;
    const {
      delivery_order_detail_reports,
      locations,
      customers,
      filter,
      filter_select,
      loading,
      loadingForeign
    } = deliveryOrderDetailReport;

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const dataSource = [];
    let expandedRowKeys = [];
    let excelDataSource = [];

    // for table and print's datasource
    Object.keys(delivery_order_detail_reports).map((item, index) => {
      return dataSource.push({
        ...delivery_order_detail_reports[index],
        key: index
      });
    });

    // for print's expandedRow
    Object.keys(delivery_order_detail_reports).map((item, index) => {
      return expandedRowKeys.push(index);
    });

    // for excel export
    delivery_order_detail_reports.map((item, index) => {
      let status;
      switch (item.status) {
        case 1:
          status = getLanguage({ id: "new" });
          break;
        case 2:
          status = getLanguage({ id: "approved" });
          break;
        case 3:
          status = getLanguage({ id: "rejected" });
          break;
        case 4:
          status = getLanguage({ id: "invoiced" });
          break;
        case 5:
          status = getLanguage({ id: "cancelled" });
          break;
        default:
          status = getLanguage({ id: "new" });
          break;
      }
      item.status_string = status;
      item.delivery_order_details.map((data, i) => {
        excelDataSource.push({
          ...item,
          ...data
        });
      });
    });

    const fileName = getLanguage({
      id: "deliveryOrderDetailReports.pageHeader"
    });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="deliveryOrderDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              columns={columns}
              componentRef={ref => (this.componentRef = ref)}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.delivery_order_details)
              }
              filter={filter_select}
              locations={locations}
              title={
                <IntlMessages id="deliveryOrderDetailReports.pageHeader" />
              }
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "zoom: 0.9;" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.delivery_order_details)
              }
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".7cm"
              scale={0.48}
              title="deliveryOrderDetailReports.pageHeader"
            />

            <ExcelWrapper
              title="deliveryOrderDetailReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={excelColumns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "deliveryOrders.no" }) +
                ", " +
                getLanguage({ id: "salesOrders.salesOrderNo" }) +
                ", " +
                getLanguage({ id: "createdBy" }) +
                ", " +
                getLanguage({ id: "confirmedBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                disabled={
                  !permissions.includes("Delivery Order Detail Report")
                    ? true
                    : false
                }
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="deliveryOrderReports.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "delivery_order_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="delivery_order_date">
                            <IntlMessages id="deliveryOrderReports.filter.date" />
                          </Option>
                          <Option key={2} value="delivery_order_no">
                            <IntlMessages id="deliveryOrderNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="customer_name">
                            <IntlMessages id="sidebar.customer" />
                          </Option>
                          <Option key={5} value="total_qty_order">
                            <IntlMessages id="totalQtyOrder" />
                          </Option>
                          <Option key={6} value="total_qty_delivered">
                            <IntlMessages id="totalQtyDelivered" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder={getLanguage({ id: "status" })}
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "approved" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "rejected" })}
                          </Option>
                          <Option value={4}>
                            {getLanguage({ id: "invoiced" })}
                          </Option>
                          <Option value={5}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          disabled={
                            !permissions.includes(
                              "Delivery Order Detail Report"
                            )
                              ? true
                              : false
                          }
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : delivery_order_detail_reports.length === 0 &&
              !this.props.deliveryOrderDetailReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="deliveryOrderDetailReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    defaultExpandAllRows={true}
                    expandedRowRender={record =>
                      expandedRowRender(record.delivery_order_details)
                    }
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    deliveryOrderDetailReport: state.deliveryOrderDetailReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  DeliveryOrderDetailReport
);
